@media (min-width: 1000px) {
  .layout--uni-2col {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 60px;
    display: grid;
  }

  .layout--uni-2col.grid-1-2 {
    grid-template-columns: minmax(0, 1fr) minmax(0, 2fr);
  }

  .layout--uni-2col.grid-2-1 {
    grid-template-columns: minmax(0, 2fr) minmax(0, 1fr);
  }
}

.layout--uni-2col > .region > .paragraph {
  margin: 60px 0;
}

@media (max-width: 999.98px) {
  .layout--uni-2col > .region > .paragraph {
    margin: 40px 0;
  }
}

@media (min-width: 1000px) {
  .layout--uni-2col > .region > .paragraph:first-of-type {
    margin-top: 0;
  }

  .layout--uni-2col > .region > .paragraph:last-of-type {
    margin-bottom: 0;
  }
}

